export default {
    name:'center',
    // 获取登录名
    computed:{
        loginname:function(){
            return this.$store.state.loginname;
        }
    },
    created:function(){
        // 跳转到用户界面 使用编程式路由
        // this.$router.push这个方法回想history添加一个记录，点击后退返回到上一个界面
        // this.$router.replace 这个方法不会向history添加一个记录，点击后退返回上一个点击的界面
        this.$router.replace('/user/'+this.loginname);
    }
}



